











import Vue from 'vue';
export default Vue.extend({
  name: 'btn',
  props: {
    fontsize: {
      type: Number,
      default: 0
    },
    padding: {
      type: String,
      default: '0'
    }
  },
  methods: {
    event: function() { //调用方法
      this.$emit('event');
    }
  }
})
